import {Component, OnInit} from '@angular/core';
import {ControllerService} from '../services/controller.service';
import {AlertService} from '../services/alert.service';
import {PRIVILEGES} from '../services/constants.service';

@Component({
  selector: 'app-admin-login',
  templateUrl: './admin-login.page.html',
  styleUrls: ['./admin-login.page.scss'],
})
export class AdminLoginPage implements OnInit {

  form = {
    username: '',
    password: '',
  };

  constructor(private api: ControllerService, private toast: AlertService) { }

  ngOnInit() {
  }

  login() {
    if (this.api.login(this.form.username, this.form.password, PRIVILEGES.ADMIN)) {
      this.toast.show('Log In Successfully')
        .then(() => {
          location.assign('/admin-home');
        });
    } else {
      this.toast.show('Invalid Credentials.');
    }
  }

}
